@import '../scss/color';

.detail {
  display: flex;
  margin-top: 70px;

  .content {
    display: block;
    width: calc(100vw - 300px);

    .bread {
      padding: 1rem;
      text-align: left;
      border: 0;
      border-bottom: 1px solid #eee;
      border-radius: 0;
    }

    .block {
      overflow: scroll;
      display: block;
      padding: 2rem;
      width: 100%;
      height: calc(100vh - 70px - 55px);
      background-color: $light-gray;
    }
  }
}

